@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

header {
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 5;
}

.Container {
    background-color: #920000;
    width: 100%;
    display: flex;
    flex-direction: row;
    height: 100px;

    /*box-shadow: 0px 10px 11px 3px rgba(0,0,0,0.5);*/
    /*-webkit-box-shadow: 0px 10px 11px 3px rgba(0,0,0,0.5);*/
    /*-moz-box-shadow: 0px 10px 11px 3px rgba(0,0,0,0.5);*/
}

.LogoContainer {
    width: 30%;
    height: 100%;
}

.Logo {
    height: auto;
    width: 70%;
    object-fit: cover;
    background-color: #920000;
    padding: 20px;
    border-radius: 25px;
}

.NavItem {
    width: 100%;
    height: auto;
}

.NavButton {
    font-family: 'Lobster', cursive;
    color: white;
    font-size: 50px;
    transition: all 0.75s;
}

.NavButton:hover {
    font-size: 60px;
    text-shadow: 0px 0px 25px rgba(0,0,0,1);
}

.Link {
    text-decoration: none;
    color: white;
}

.OuterButtons {
    display: flex;
    width: 35%;
    height: auto;
    align-items: center;
}

/* Tablet Styling */
@media screen and (max-width: 1300px) {
    .Container {
        margin-bottom: 50px;
    }
    .NavButton {
        font-size: 35px;
    }
    .NavButton:hover {
        font-size: 45px;
    }
}

/* Phone Styling */
@media screen and (max-width: 925px) {
    .Container {
        top: auto;
        margin-bottom: 50px;
        transition: all 2s;
    }
    .OuterButtons {
        display: none;
    }
    .Container {
        align-items: center;
        justify-content: space-around;
    }
    .Logo {
        border-radius: 0;
    }
    .LogoContainer {
        width: 380px;
        display: flex;
        flex-direction: column;
        justify-items: center;
        align-items: center;
    }
    .MobileDrawer {
        display: block !important;
    }
    .MobileDrawerHandle {
        width: 80.5%;
        display: block !important;
    }
}

.MobileDrawerOpen {
    max-height: 500px !important;
}

.MobileDrawer {
    display: none;
    height: auto;
    max-height: 0;
    transition: all 0.25s ease-in;
    width: 100%;
    background-color: #920000;
    overflow: hidden;
}

.MobileDrawerItems {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin: 10px;
    width: auto;
}

.MobileDrawerItem {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 10px;
    color: white;
    font-size: 30px;
    cursor: pointer;
}

.MobileDrawerItem:hover {
    background: #6a0000;
}

.MobileDrawerItem:active {
    box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.75) inset;
    -webkit-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.75) inset;
    -moz-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.75) inset;
}

.MobileDrawerHandle {
    display: none;
    color: white;
    background: #920000;
    margin-top: -5px;
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
    cursor: pointer;
}

.MobileDrawerHandle:hover {
    background-color: #6a0000;
}