.CustomCarousel {

}

.carousel-slider {
    background-color: rgba(0, 0, 0, 0.75);
}

.carousel-status {
    top: -20px !important;
    font-size: 20px !important;
}

.thumbs-wrapper {
    margin: 0 20px !important;
}

/*This removes carousel dots*/
.control-dots {
    display: none;
}

.CarouselItem {
    height: 500px;
}

.CarouselItemImg {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.CottageAddress {
    padding: 10px;
    font-size: 25px;
}

.Socials {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 5px;
}

.SocialItem {
    width: 50%;
}

.SocialItemImg {
    width: 85%;
    object-fit: contain;
    transition: width 0.5s;
    cursor: pointer;
}

.SocialItemImg:hover {
    /*width: 90%;*/
}

.Description {
    font-size: 20px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 10px;
    text-align: left;
    text-indent: 20px;
}