.ContentContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.StyledContainer {
    border-radius: 25px;
    background-color: rgba(255, 255, 255, 0.5);
    max-width: 1000px;
    margin-top: 10px;
    margin-bottom: 10px;
}

.CottageSelector {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    border-radius: 25px;
}

.SelectableCottage {
    font-family: 'Lobster', cursive;
    font-size: 25px;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.75);
    transition: all 0.2s;
    cursor: pointer;
}

.SelectableCottage:hover {
    background-color: rgb(96, 96, 96);
    color: white;
}

.SelectableCottage.active {
    background-color: rgb(53, 53, 53);
    color: white;
}

.Marquette {
    display: block;
    transition: display 0.5s;
}

.Loyola {
    display: none;
    transition: display 0.5s;
}

@media screen and (max-width: 800px) {
    .ContentContainer {
        display: block;
    }

}